.AppointmentDetail {
  &-wrapper {
    padding: 30px;
    background: var(--petrol-5);
    border: 1px solid #ffffff;
    box-sizing: border-box;
    border-radius: 32px;
  }

  &-title {
    font-weight: 500;
    font-size: 27px;
    color: var(--nightblue-100);
  }

  &-btn {
    margin-top: 30px;
    height: 45px;
    font-size: 15px;
    font-weight: 500;
  }
  &-btn > button {
    font-size: 15px;
    font-weight: 500;
  }
}
