.AppointmentDetailLayout {
  &-area {
    // padding-bottom: 110px;
    @media screen and (max-width: 480px) {
      margin: 0;
    }
  }
  &-header {
    min-height: 36px;
    display: flex;
    justify-content: space-between;
    button {
      i {
        margin-right: 8px;
        font-size: 20px;
      }
    }
  }
  &-wrapperContainer {
    border-radius: 30px;
    display: flex;
    flex-direction: column;
  }

  &-container {
    margin-top: 60px;
    padding: 30px 0;
    width: 100%;
  }

  &-title {
    margin: 0;
    color: #fff;
    font-size: 34px;
    line-height: 38px;
    text-align: center;
    letter-spacing: -0.01em;
    word-break: break-word;
  }
}
